import React, { Component, Suspense } from 'react';
import DisplayUrl from '../../../PublishCommonComponents/DisplayUrl';
import { DisplayURL } from '../../../../../Services/Api'
class ScreenCloudUrl extends Component {

    render() {
        const { data } = this.props
        return  <div className="card text-decoration-none">
                    <div className="card-body p-lg-7">
                        <div className="m-auto">
                            <div className="embed_code">
                                <h5 className="mb-5 fw-bolder">To Display The Social Wall With ScreenCloud</h5>
                                <h6>Step 1 : Copy The Display Wall URL</h6>
                                <DisplayUrl url={`${DisplayURL}${data && Object.keys(data).length > 0 ? data.wallId : null}`}/> 
                                <h6 className="mb-5">Step 2: Add The Social Wall To ScreenCloud's App In The Following Way</h6>
                                <ul className="list-unstyled list-points fs-7">
                                    <li className="mb-3">Start by Logging in to your ScreenCloud account</li>
                                    <li className="mb-3">Now, go to the 'Apps' section and click on 'App Store'</li>
                                    <li className="mb-3">Select 'Taggbox Display'</li>
                                    <li className="mb-3">Next click on <span className="fw-bold">'New Instance'</span> from the upper right corner.</li>
                                    <li className="mb-3">Lastly, enter your 'Taggbox Social Wall URL', apply themes and set the number of posts to show (if required).</li>
                                    <li className="mb-3">Click on 'Save'</li>
                                    <li className="mb-3">Congratulations, you've successfully added the social wall in ScreenCloud' app.</li>
                                </ul>
                                <div className="d-flex alert alert-info py-3 mb-2"><p className="mb-0 text-gray-800">Note: To display your social wall, you can set your app instance directly to your screens, add it into a playlist, or schedule it into a channel.</p></div>
                            </div>
                        </div>
                    </div>
                </div>
    }
}

export default ScreenCloudUrl;